<template>
  <material-card
    color="primary"
    icon="mdi-cart-plus"
    flat
  >
    <template #title>
      Create New Order
    </template>
    <v-form>
      <v-container class="py-0">
        <v-row>
          <v-col
            cols="12"
            md="3"
          >
            <!-- <div class="text-h3">
              Delivery Data
            </div> -->
            <v-select
              v-model="form.selectedUser"
              :items="data"
              item-text="name"
              item-value="id"
              label="Select User"
              outlined
              clearable
              @change="pickupFee()"
            />
            <v-text-field
              v-model="form.receiverName"
              label="Receiver Name"
              outlined
              clearable
            />
            <v-text-field
              v-model="form.receiverPhone"
              label="Receiver Phone"
              outlined
              clearable
            />
            <v-text-field
              v-model="form.receiverPhone2"
              label="Receiver Phone 2"
              outlined
              clearable
            />
            <v-select
              v-model="form.selectedCity"
              :items="city"
              item-text="nama"
              item-value="city_id"
              label="Select City"
              outlined
              persistent-hint
              @change="filterDistrict()"
            />
            <v-select
              v-model="form.selectedDistrict"
              :items="filteredDistrict"
              item-text="nama"
              item-value="id"
              label="Select District"
              :disabled="form.selectedCity === null"
              outlined
              persistent-hint
              clearable
              @change="filterVillage()"
            />
          </v-col>
          <v-col
            cols="12"
            md="3"
          >
            <v-select
              v-if="form.selectedDistrict !== null"
              v-model="form.selectedVillage"
              :items="filteredVillage"
              item-text="nama"
              item-value="id"
              label="Select Village"
              outlined
              persistent-hint
              clearable
              @change="deliveryFee()"
            />
            <!-- disabled before pick disctrict -->
            <v-select
              v-else
              v-model="form.selectedVillage"
              :items="filteredVillage"
              item-text="nama"
              item-value="id"
              label="Select Village"
              disabled
              outlined
              persistent-hint
              @change="deliveryFee()"
            />
            <v-textarea
              v-model="form.receiverAddress"
              label="Receiver Address"
              rows="5"
              outlined
              clearable
            />
            <!-- <v-textarea
              v-model="form.descriptionAddress"
              label="Description Address"
              rows="2"
              outlined
              clearable
            /> -->
            <v-select
              v-model="form.selectedMethod"
              :items="methods"
              item-text="method"
              item-value="id"
              label="Select Payment Method"
              outlined
              clearable
            />
            <v-text-field
              v-model="form.productName"
              label="Product Name"
              outlined
              clearable
            />
            <v-text-field
              v-model="form.weight"
              label="Weight"
              type="number"
              outlined
              clearable
              @input="countFee"
            />
          </v-col>
          <v-col
            cols="12"
            md="3"
          >
            <!-- <div class="text-h3">
              Product
            </div> -->
            <v-text-field
              v-model="form.volume"
              label="Volume"
              type="number"
              outlined
              clearable
            />
            <vuetify-money
              v-model="form.delivery_fee"
              label="Delivery Fee"
              readonly
              :outlined="outlined"
              :options="options"
            />
            <vuetify-money
              v-model="form.price"
              :label="label"
              :readonly="readonly"
              :disabled="disabled"
              :outlined="outlined"
              :clearable="clearable"
              :options="options"
            />
            <v-textarea
              v-model="form.description"
              label="Description"
              type="number"
              rows="4"
              outlined
              clearable
            />
            <v-select
              v-model="form.delivery_driver"
              :items="drivers"
              item-text="name"
              item-value="id"
              label="Select Delivery Driver"
              outlined
              clearable
            />
          </v-col>
          <v-col
            cols="12"
            md="3"
          >
            <v-file-input
              v-model="form.photo"
              accept="image/jpeg"
              prepend-icon=""
              label="Photo"
              clearable
              outlined
              @change="preview($event)"
            />
            <v-card
              class="d-flex justify-space-around pa-4"
              style="border:dashed; border-color: gray;"
            >
              <v-img
                v-if="imgUrl"
                class="preview"
                :src="imgUrl"
              />
              <v-card-text
                v-else
                class="text-center"
                style="height:185px"
              >
                Image Preview Area
              </v-card-text>
            </v-card>
            <v-btn
              color="primary"
              min-width="150"
              class="float-right mt-5px"
              @click="saveOrder"
            >
              Create Order
            </v-btn>
          </v-col>
        </v-row>
      </v-container>
    </v-form>
  </material-card>
</template>
<script>
  import axios from 'axios'
  export default {
    _props: ['data',
             'district',
             'village',
             'methods',
             'del_fee_list',
             'category_id',
             'city'],
    get props () {
      return this._props
    },
    set props (value) {
      this._props = value
    },

    data: () => ({
      imgUrl: null,
      filteredVillage: [],
      filteredDistrict: [],
      form: {
        selectedUser: null,
        selectedCity: null,
        selectedDistrict: null,
        selectedVillage: null,
        selectedMethod: null,
        productName: null,
        weight: null,
        volume: null,
        price: null,
        description: null,
        photo: null,
        receiverName: null,
        receiverAddress: null,
        descriptionAddress: null,
        receiverPhone: null,
        receiverPhone2: null,
        delivery_fee: 0,
        delivery_driver: null,
      },
      value: 0,
      label: 'Price',
      readonly: false,
      additional_fee: 0,
      additional_fee2: 0,
      disabled: false,
      outlined: true,
      drivers: [],
      clearable: true,
      options: {
        locale: 'pt-BR',
        prefix: 'Rp ',
        suffix: ' ',
        length: 12,
        precision: 0,
      },
    // feelist: this.del_fee_list,
    }),

    mounted () {
      this.getDrivers()
    },

    methods: {
      preview (e) {
        if (this.form.photo !== null) {
          this.form.photo = e
          this.imgUrl = URL.createObjectURL(this.form.photo)
        } else {
          this.imgUrl = null
        }
      },

      getDrivers () {
        axios.get('admin/driver').then(res => {
          this.drivers = res.data.driver
        })
      },

      countFee () {
        this.form.delivery_fee = 0
        const w = this.form.weight
        const list = this.del_fee_list
        list.forEach(item => {
          if (w > parseInt(item.from_weight) && w <= parseInt(item.to_weight)) {
            this.form.delivery_fee = parseInt(item.price) + parseInt(this.additional_fee) + parseInt(this.additional_fee2)
          }
        })
      },

      saveOrder () {
        this.$swal.showLoading()
        const form = new FormData()
        form.append('name', this.form.productName)
        form.append('weight', this.form.weight)
        form.append('volume', this.form.volume)
        form.append('price', this.form.price)
        form.append('description_order', this.form.description)
        form.append('photo', this.form.photo)
        form.append('receiver_name', this.form.receiverName)
        form.append('receiver_phone', this.form.receiverPhone)
        form.append('receiver_phone2', this.form.receiverPhone)
        form.append('receiver_address', this.form.receiverAddress)
        form.append('description_address', this.form.descriptionAddress)
        form.append('city', this.form.selectedCity)
        form.append('district', this.form.selectedDistrict)
        form.append('village', this.form.selectedVillage)
        form.append('payment_method', this.form.selectedMethod)
        form.append('user_id', this.form.selectedUser)
        form.append('delivery_fee', this.form.delivery_fee)
        form.append('category_id', this.category_id)
        form.append('delivery_driver', this.form.delivery_driver)
        axios
          .post('admin/order', form)
          .then(() => {
            this.$swal('Data Created Successfully', '', 'success')
            this.form.productName = null
            this.form.weight = 0
            this.form.volume = 0
            this.form.price = 0
            this.form.description = null
            this.form.photo = null
            this.form.receiverPhone = null
            this.form.receiverPhone2 = null
            this.form.receiverName = null
            this.form.receiverAddress = null
            this.form.descriptionAddress = null
            this.form.selectedCity = null
            this.form.selectedDistrict = null
            this.form.selectedVillage = null
            this.form.selectedMethod = null
            this.form.selectedUser = null
            this.form.delivery_driver = null
          })
          .catch(error => {
            console.log(error)
          })
      },

      filterVillage () {
        this.filteredVillage = []
        this.filteredVillage = this.village.filter(
          village => village.kecamatan_id === this.form.selectedDistrict,
        )
      },

      filterDistrict () {
        this.filteredDistrict = []
        this.filteredDistrict = this.district.filter(
          district => district.kabupaten_id === this.form.selectedCity,
        )
      },

      deliveryFee () {
        if (this.form.selectedCity !== '7371') {
          axios.get('admin/special-delivery-fee/' + this.form.selectedVillage)
            .then((res) => { this.additional_fee = res.data })
          this.countFee()
        } else {
          this.additional_fee = 0
          this.countFee()
        }
      },

      pickupFee () {
        axios.get('admin/special-pickup-fee/' + this.form.selectedUser)
          .then((res) => { this.additional_fee2 = res.data })
        this.countFee()
      },
    },
  }
</script>

<style>
.preview {
  max-width: 200px;
  max-height: 220px;
  justify-content: right;
}
</style>
